import * as React from "react"
import { useDeviceInfo } from "@rp/react/web/common/hooks"
import { Box, Typography } from "@rp/react/web/mui/core"
import { BOTTOM_NAV_HEIGHT } from "../NavBars"
import { VerticalScrollStepperProps } from "./VerticalScrollStepper"
import { VerticalScrollStepperBar } from "./VerticalScrollStepperBar"
import { VerticalScrollStepperFooter } from "./VerticalScrollStepperFooter"
import {
  VERTICAL_SCROLL_HEADER_CLASS_NAME,
  VERTICAL_SCROLL_STEP_CLASS_NAME,
} from "./verticalScrollStepperConstants"

const VerticalScrollStepperWrapper = ({
  title,
  steps,
  children,
  nextDisabled,
  handleComplete,
  completeButtonText,
  activeStep,
  onStepChange,
  offsetTop = 0,
  stepLabel,
  isNextLoading = false,
  withKeyboardShortcuts = false,
}: VerticalScrollStepperProps) => {
  const { isMobile } = useDeviceInfo()
  const MARGIN_X = isMobile ? 0 : 55
  const WIDTH = isMobile ? "100%" : 810
  const isCompleteStep = !(activeStep < steps - 1)
  return (
    <Box sx={{ maxWidth: WIDTH, margin: "0 auto" }}>
      <Box
        className={VERTICAL_SCROLL_HEADER_CLASS_NAME}
        sx={{
          py: 1,
          position: "sticky",
          top: -1 + offsetTop,
          left: 0,
          backgroundColor: "surface.background",
          zIndex: 2,
          margin: `0 ${MARGIN_X}px`,
        }}
      >
        <Box sx={{ display: "flex", height: "27px", alignItems: "center", pt: 1 }}>
          <Typography role="heading" sx={{ fontSize: 20, fontWeight: 700, flexGrow: 1 }}>
            {title}
          </Typography>
          <Typography sx={{ fontSize: 12, color: "grey.300" }}>
            {stepLabel ?? `Step ${Math.min(steps, activeStep + 1)}/${steps}`}
          </Typography>
        </Box>
        <VerticalScrollStepperBar steps={steps} activeStep={activeStep} />
      </Box>
      <Box
        sx={{
          display: "flex",
          flexDirection: "column",
          margin: `0 ${MARGIN_X}px`,
          paddingBottom: isMobile ? `${BOTTOM_NAV_HEIGHT}px` : 0,
          [`& .${VERTICAL_SCROLL_STEP_CLASS_NAME}`]: {
            transition: "opacity 0.2s 0.2s",
          },
          [`& .${VERTICAL_SCROLL_STEP_CLASS_NAME}:last-of-type`]: {
            opacity: 0,
          },
          [`& .${VERTICAL_SCROLL_STEP_CLASS_NAME}.visible`]: {
            opacity: 1,
          },
        }}
      >
        {children}
      </Box>
      <VerticalScrollStepperFooter
        activeStep={activeStep}
        width={WIDTH}
        onForward={isCompleteStep ? handleComplete : () => onStepChange?.(activeStep + 1)}
        onBack={onStepChange ? () => onStepChange(activeStep - 1) : undefined}
        disabled={nextDisabled}
        buttonLabel={isCompleteStep ? completeButtonText : "Next"}
        isLoading={isNextLoading}
        withKeyboardShortcuts={withKeyboardShortcuts}
      />
    </Box>
  )
}

export { VerticalScrollStepperWrapper }
